import { Alert, Slide } from "@mui/material";
import Backdrop from "@mui/material/Backdrop";
import CircularProgress from "@mui/material/CircularProgress";
import Snackbar from "@mui/material/Snackbar";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import React, { useReducer, useState, createContext } from "react";
import { HashRouter } from "react-router-dom";
import Dashborad from "./Dashboard/Dashborad";
import LoginForm from "./LoginForm/LoginForm";
import { ConfirmationDialog } from "./Tools/confirmationDialog";

export const StatusContext = createContext({});
const theme = createTheme({
  palette: {
    primary: {
      main: "#FF5656",
      bg: "#FF56564f",
      lightBg:"#FF565612"
    },
    secondary: {
      main: "#20AAE2",
      bg: "#20AAE24f",
      lightBg:"#20AAE212"
    },
    third: {
      main: "#5F00BA",
      bg: "#5F00BA4f",
    },
    forth: {
      main: "#FB9300",
      bg: "#FB93004f",
    },
    success: {
      main: "#43c54a",
      bg: "#43c54a4f",
      contrastText: "#FFFFFF",
    },
    error: {
      main: "#FF5656",
      contrastText: "#FFFFFF",
    },
    text:{
      primary:"rgb(99, 115, 129)",
    }
  },
});

function TransitionDown(props) {
  return <Slide {...props} direction="down" />;
}
const Alerts = (props) => {
  return (
    <>
      <Snackbar
        open={props.payload.open}
        autoHideDuration={1200}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        TransitionComponent={TransitionDown}
        onClose={props.handleClose}
      >
        <Alert
          onClose={props.handleClose}
          severity={props.payload.type}
          sx={{ width: "100%" }}
          variant="filled"
        >
          {props.payload.message ?? "Hello there!"}
        </Alert>
      </Snackbar>
    </>
  );
};

export const LogginContext = React.createContext({});
function App() {
  const [Logged, setLogged] = useState(
    localStorage.getItem("logged") === "true" ? true : false ?? false
  );
  const Loggin = () => {
    setLogged(true);
    localStorage.setItem("logged", true);
  };
  const Logout = () => {
    setLogged(false);
    localStorage.clear();
  };

  const reducer = (state, action) => {
    switch (action.type) {
      case "showLoading":
        return { ...state, loading: action.payload };
      case "showSuccess":
        return {
          ...state,
          snackbar: { open: true, type: "success", message: action.payload },
        };
      case "showInfo":
        return {
          ...state,
          snackbar: { open: true, type: "info", message: action.payload },
        };
      case "showError":
        return {
          ...state,
          snackbar: { open: true, type: "error", message: action.payload },
        };
      case "hideSnackbar":
        return { ...state, snackbar: { ...state.snackbar, open: false } };
      case "showDialog":
        return {
          ...state,
          confirmationDialog: { ...action.payload, open: true },
        };
      case "closeDialog":
        return {
          ...state,
          confirmationDialog: { ...state.confirmationDialog, open: false },
        };
      default:
        throw new Error();
    }
  };
  const [uiState, dispatch] = useReducer(reducer, {
    loading: false,
    snackbar: {
      open: false,
      message: "a message",
      type: "error", // success | error
    },
    confirmationDialog: {
      open: false,
      title: "a title",
      message: "a message",
      callback: () => {},
      type: "positive", // positive | negative
      buttonLabel: "valider",
    },
  });
  const handleClose = (event) => {
    dispatch({ type: "hideSnackbar" });
  };

  return (
    <StatusContext.Provider value={{ dispatch }}>
      <ThemeProvider theme={theme}>
        <Alerts payload={uiState.snackbar} handleClose={handleClose} />
        <Backdrop sx={{ color: "#fff", zIndex: 1202 }} open={uiState.loading}>
          <CircularProgress color="inherit" />
        </Backdrop>
        <ConfirmationDialog
          open={uiState.confirmationDialog.open}
          handleClose={() => dispatch({ type: "closeDialog" })}
          payload={uiState.confirmationDialog}
        />
        <LogginContext.Provider value={{ Loggin, Logout }}>
          <HashRouter>
            {Logged === true ? <Dashborad /> : <LoginForm />}
          </HashRouter>
        </LogginContext.Provider>
      </ThemeProvider>
    </StatusContext.Provider>
  );
}

export default App;
