import React from "react";
import { Grid } from "@mui/material";

export default function GridFactory({ gridSize, child, spacing, items, values }) {
  return (
    <Grid container spacing={spacing} >
      {items?.map((item, index) => (
        <Grid
          item
          key={index}
          xs={gridSize.xs}
          sm={gridSize.sm}
          md={gridSize.md}
          lg={gridSize.lg}
          sx={{display:'flex'}}
        >
          {React.cloneElement(child, { item: item, values : values, Tindex:index })}
        </Grid>
      ))}
    </Grid>
  );
}
