import { Container } from "@mui/material";
import Box from "@mui/material/Box";
import { styled } from "@mui/material/styles";
import Toolbar from "@mui/material/Toolbar";
import React, { useContext } from "react";
import { Route, Routes } from "react-router-dom";
import { DraweContext } from "./Drawer/Drawer";
import Overview from "./Routes/Overview/Overview";
import Chat from './Routes/Chat/chat';
import notfound from "../assets/pics/404.svg"
import Tours from './Routes/Tours/Tours';

const MainBox = styled(Box, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  overflow:"hidden",
  flexGrow: 1,
  ...(open && {
    [theme.breakpoints.down("sm")]: {
        display: "none",
      },
 
  }),
}));

const Main = () => {
  const { open } = useContext(DraweContext);

  return (
    <MainBox open={open} component="main">
      <Toolbar />
      <Container maxWidth="xl" disableGutters sx={{p:"16px"}}>
       <WebappRoutes/>
      </Container>
    </MainBox>
  );
};


const WebappRoutes = () => {
  return (
    <Routes>
      <Route path="/" element={<Overview />}/>
      <Route path="groups" element={<>groups</>}/>
      <Route path="tours" element={<Tours/>}/>
      <Route path="users" element={<>users</>}/>
      <Route path="chat" element={<Chat/>}/>
      <Route path="advertisement" element={<>advertisement</>}/>
      <Route path="*" element={<NotFound/>}/>
    </Routes>
  )
}

const NotFound = () => {
  return (
    <Box sx={classes.notFound}/>
  )
}
const classes = {
  notFound:{
    height: "calc(100vh - 120px)",
    width:"100%",
    background: `url(${notfound}) center no-repeat`,
    backgroundSize: "contain",
  }
}

export default Main;
