import SendRoundedIcon from "@mui/icons-material/SendRounded";
import {
  Avatar,
  IconButton,
  Slide,
  Stack,
  TextField,
  Typography,
  useMediaQuery,
} from "@mui/material";
import { Box } from "@mui/system";
import { useEffect, useRef, useState, useCallback } from "react";
import ChevronLeftRoundedIcon from "@mui/icons-material/ChevronLeftRounded";
import { useDropzone } from "react-dropzone";

const MessagesSide = ({ Livechat, setLivechat, CloseMsg }) => {
  const [value, setValue] = useState("");
  const ref = useRef(null);

  const [LiveMessages, setMessages] = useState(Livechat?.message ?? []);
  const Handlchage = (e) => {
    setValue(e.target.value);
  };
  // DragAndDrop

  const onDrop = useCallback((acceptedFiles) => {
    // Do something with the files
    console.log(acceptedFiles);
  }, []);
  const DND = useDropzone({ onDrop: onDrop, noClick: true });

  ///////////////
  const SendMessage = () => {
    if (value === "") return;
    let msgs = LiveMessages ?? [];
    msgs = [...msgs, { msg: value, who: "support" }];
    setValue("");
    setMessages([...msgs]);
    setTimeout(() => {
      ref.current.scrollIntoView({
        behavior: "smooth",
        block: "end",
        inline: "end",
      });
    });
  };
  useEffect(() => {
    setMessages(Livechat?.message);
  }, [Livechat?.message]);

  return Livechat ? (
    <>
      <MessageHead Livechat={Livechat} CloseMsg={CloseMsg} />
      <Messages LiveMessages={LiveMessages} inref={ref} DND={DND} />

      <InputMessage
        value={value}
        Handlchage={Handlchage}
        SendMessage={SendMessage}
      />
    </>
  ) : (
    <>noMsgs</>
  );
};

const Messages = ({ LiveMessages, inref, DND }) => {
  const { isDragActive, getRootProps, getInputProps } = DND;

  return (
    <div style={{ position: "relative" }} {...getRootProps()}>
      <DropHere isDragActive={isDragActive} />
      <Box sx={classes.StackMsgs}>
        <input {...getInputProps()} />
        <StackMsgs LiveMessages={LiveMessages} inref={inref} />
      </Box>
    </div>
  );
};

const DropHere = ({ isDragActive }) => {
  return (
    <Slide in={isDragActive} direction="up" timeout={{enter:50, exit:100}}>
      <Box sx={classes.DropShadow}>
        <Box sx={classes.innerDnd}>
          <Typography
            sx={{ color: "#bdbdbd", fontSize: "clamp(1rem, 5vw, 55px)" }}
          >
            DRAG FILE HERE
          </Typography>
        </Box>
      </Box>
    </Slide>
  );
};

const StackMsgs = ({ LiveMessages, inref }) => {
  return (
    <>
      <Stack direction="column" spacing={2}>
        {LiveMessages &&
          LiveMessages.map((m, index) =>
            m.who === "client" ? (
              <RightMsg key={index} msg={m.msg} time={m.time} />
            ) : (
              <LeftMsg key={index} msg={m.msg} time={m.time} />
            )
          )}
      </Stack>
      <div ref={inref} style={{ height: "1px" }} />
    </>
  );
};
const RightMsg = ({ time, msg }) => {
  return (
    <Typography sx={classes.ChatMessageLeft}>
      {msg}
      <Typography sx={classes.Time} variant="span">
        12:32
      </Typography>
    </Typography>
  );
};
const LeftMsg = ({ time, msg }) => {
  return (
    <Typography sx={classes.ChatMessageRight}>
      {msg}
      <Typography sx={classes.Time} variant="span">
        12:32
      </Typography>
    </Typography>
  );
};

const MessageHead = ({ Livechat, CloseMsg }) => {
  const matches = useMediaQuery((theme) => theme.breakpoints.down("lg"));

  return (
    <Box sx={classes.Head}>
      <Stack direction="row" spacing={1} sx={{ paddingLeft: "8px" }}>
        {matches ? (
          <IconButton onClick={CloseMsg}>
            <ChevronLeftRoundedIcon />
          </IconButton>
        ) : (
          false
        )}
        <Box sx={classes.user}>
          <Avatar alt="Client" src={Livechat.avatar} />
        </Box>
      </Stack>
      <Stack
        paddingLeft="1rem"
        direction="column"
        justifyContent="center"
        spacing={0.5}
      >
        <Typography sx={classes.ClientName}>{Livechat.name}</Typography>
        <Typography sx={classes.ClientMessage}>
          {Livechat.online ? "Online" : "Offline"}
        </Typography>
      </Stack>
      {/* <Divider/> */}
    </Box>
  );
};

const InputMessage = ({ value, Handlchage, SendMessage }) => {
  const EnterSubmit = (e) => {
    const key = e.which ?? 0;
    if (key === 13) SendMessage();
  };
  return (
    <Box sx={classes.inputBox}>
      <TextField
        placeholder="Send message..."
        id="outlined"
        sx={classes.SendInput}
        value={value}
        onKeyPress={EnterSubmit}
        onChange={Handlchage}
        InputProps={{
          endAdornment: (
            <IconButton size="small" onClick={SendMessage}>
              <SendRoundedIcon sx={{ color: "#000000a9" }} />
            </IconButton>
          ),
        }}
      />
    </Box>
  );
};

