import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import { Box, Divider, Grid, Paper, Typography, Stack } from "@mui/material";
import React from "react";
import LocalShippingRoundedIcon from "@mui/icons-material/LocalShippingRounded";
import { useTheme } from "@mui/material/styles";
import useMediaQuery from "@mui/material/useMediaQuery";
import Pagination from "@mui/material/Pagination";
import MoreVertRoundedIcon from "@mui/icons-material/MoreVertRounded";
import { DefaultTours } from "../../../assets/MasterData";
import CancelOutlinedIcon from "@mui/icons-material/CancelOutlined";
const STATUS = { ACTIVE: true, DESICTIVATED: false };

export default function ToursTracking() {
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down("md"));
  return (
    <>
      {matches ? <Mobile /> : <Web />}
      <Pagination
        count={10}
        variant="outlined"
        shape="rounded"
        sx={{
          "& .MuiPagination-ul": {
            justifyContent: "center",
          },
        }}
      />
    </>
  );
}

const Mobile = () => {
  return (
    <>
      <Grid
        container
        sx={{ py: "24px" }}
        spacing={2}
        justifyContent="space-evenly"
      >
        {DefaultTours.map((item, index) => (
          <Grid item key={index} sx={{ flexGrow: 1 }}>
            <Box sx={classes.userCard}>
              <Stack direction="column" spacing={1}>
                <Stack
                  direction="row"
                  justifyContent="space-between"
                  alignItems="center"
                  spacing={2}
                >
                  <Typography sx={classes.carduserName}>
                    Toure : {item.Tname}
                  </Typography>
                  <StatusHandler status={item.status} />
                </Stack>
                <Typography sx={classes.cardPhone}>{item.phone}</Typography>
                <Stack
                  direction="row"
                  spacing={2}
                  //   justifyContent="space-between"
                  alignItems="center"
                >
                  <Typography sx={classes.cardPhone}>
                    {item.startDate}
                  </Typography>
                  <Divider
                    sx={{ flexGrow: 1, backgroundColor: "secondary.main" }}
                  />
                  <Typography sx={classes.cardPhone}>{item.endDate}</Typography>
                </Stack>
              </Stack>
            </Box>
          </Grid>
        ))}
      </Grid>
    </>
  );
};

const Web = () => {
  return (
    <Paper sx={classes.paperRoot}>
      <HeadFactory />
      <Divider />
      <TableBody />
    </Paper>
  );
};

const HeadFactory = () => {
  return (
    <Grid container sx={{ py: "16px", paddingLeft: "16px" }} spacing={2}>
      {head.map((item, index) => (
        <Grid item xs={item.gridSize} key={index}>
          <Typography sx={classes.HeadTitle}>{item.label}</Typography>
        </Grid>
      ))}
    </Grid>
  );
};

const TableBody = () => {
  return DefaultTours.map((item, index) => (
    <div key={index}>
      <Grid container sx={classes.gridHover} spacing={2} alignItems="center">
        <Grid item xs={1}>
          <Typography sx={classes.bodyitemsid}>{index + 1}</Typography>
        </Grid>
        <Grid item xs={2.5}>
          <Typography sx={classes.bodyitems} style={{ fontFamily: "Cairo" }}>
            {item.Tname}
          </Typography>
        </Grid>
        <Grid item xs={2.5}>
          <Typography sx={classes.bodyitems}>{item.startDate}</Typography>
        </Grid>
        <Grid item xs={2.5}>
          <Typography sx={classes.bodyitems}>{item.endDate}</Typography>
        </Grid>
        <Grid item xs={3.5}>
          <StatusHandler status={item.status} />
        </Grid>
      </Grid>
      {index + 1 === DefaultTours.length ? false : <Divider />}
    </div>
  ));
};

const StatusHandler = ({ status }) => {
  switch (status) {
    case STATUS.ACTIVE:
      return (
        <Box sx={classes.DelivredBox}>
          <CheckCircleOutlineIcon />
          <Typography sx={classes.DelivredLabel}>Activated</Typography>
        </Box>
      );
    case STATUS.DESICTIVATED:
      return (
        <Box sx={classes.OTWBox}>
          <CancelOutlinedIcon />
          <Typography sx={classes.OTWLabel}>Desactivated</Typography>
        </Box>
      );

    default:
      return <Typography>status not found</Typography>;
  }
};
