import AddCircleOutlineRoundedIcon from "@mui/icons-material/AddCircleOutlineRounded";
import CloseIcon from "@mui/icons-material/Close";
import {
  Box,
  Chip,
  DialogActions,
  DialogContent,
  Divider,
  Grid,
  IconButton,
  Stack,
} from "@mui/material";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import Typography from "@mui/material/Typography";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useFormik } from "formik";
import React, { useContext, useRef, useEffect } from "react";
import { StatusContext } from "../../../App";
import { StextField } from "../../../Tools/StyledComp";
import { ToursContext } from "./Tours";

export default function AddTourDialog({ CloseDialog, addDialog }) {
  const matches = useMediaQuery((theme) => theme.breakpoints.down("sm"));
  const { dispatch } = useContext(StatusContext);
  const { AddToure, toursData, toursMode, settoursMode, EditToure } = useContext(ToursContext);
  const defaultvalues = {
    Tname: "",
    startDate: "",
    endDate: "",
    Cname: "",
    status:false,
    Cities: [],
  };
  useEffect(() => {
    if (toursMode !== undefined) {
      let toure = toursData[toursMode] ?? defaultvalues;
      toure  = {...toure, Cname:""};
      formik.setValues(toure, false);
    }
  }, [toursMode]);

  const formik = useFormik({
    initialValues: defaultvalues,
    onSubmit: (values) => {
      toursMode !== undefined ? EditToure(values, toursMode) : AddToure(values);
      HandlClose();
      dispatch({
        type: "showSuccess",
        payload: `Toure : ${formik.values.Tname} submited`,
      });
    },
  });

  const HandlClose = () => {
    CloseDialog();
    settoursMode(undefined);
    formik.handleReset();
  }
  

  const ref = useRef();
  const AddCitie = () => {
    if (formik?.values?.Cname && formik.values.Cname !== "") {
      formik.setFieldValue("Cities", [
        ...formik.values.Cities,
        formik.values.Cname,
      ]);
      formik.setFieldValue("Cname", "");
      ref.current.focus();
    }
  };

  const DeleteCity = (cityIndex) => {
    const res = formik.values.Cities.filter(
      (value, index) => cityIndex !== index
    );
    formik.setFieldValue("Cities", [...res]);
  };

  return (
    <Dialog
      open={addDialog}
      maxWidth="md"
      fullWidth
      fullScreen={matches}
      sx={classes.Dialog}
    >
      <form onSubmit={formik.handleSubmit} style={{ width: "100%" }}>
        <DialogTitle>
          <Stack
            direction="row"
            alignItems="center"
            justifyContent="space-between"
          >
            <Typography sx={classes.DialogTitle}> { toursMode !== undefined ? "Edit " : "Add "}Toure</Typography>
            <IconButton onClick={HandlClose}>
              <CloseIcon />
            </IconButton>
          </Stack>
        </DialogTitle>
        <DialogContent sx={{ p: 0 }}>
          <List>
            <ListItem>
              <StextField
                onChange={formik.handleChange}
                value={formik.values.Tname}
                color="secondary"
                variant="outlined"
                name="Tname"
                label="Toure Name: *"
                fullWidth
              />
            </ListItem>
            <ListItem>
              <Stack
                direction="row"
                spacing={2}
                sx={{ width: "100%" }}
                alignItems="center"
              >
                <StextField
                  color="secondary"
                  variant="outlined"
                  label="Start Date: *"
                  name="startDate"
                  onChange={formik.handleChange}
                  fullWidth
                  value={formik.values.startDate}
                  sx={{ flexGrow: 1 }}
                />
                <StextField
                  color="secondary"
                  variant="outlined"
                  label="End Date: *"
                  name="endDate"
                  onChange={formik.handleChange}
                  fullWidth
                  value={formik.values.endDate}
                  sx={{ flexGrow: 1 }}
                />
              </Stack>
            </ListItem>
            <ListItem>
              <Stack
                direction="row"
                spacing={2}
                sx={{ width: "100%" }}
                alignItems="center"
              >
                <StextField
                  inputRef={ref}
                  color="secondary"
                  variant="outlined"
                  label="City Name: *"
                  name="Cname"
                  onChange={formik.handleChange}
                  fullWidth
                  value={formik.values.Cname}
                  sx={{ flexGrow: 1 }}
                  InputProps={{
                    endAdornment: (
                      <IconButton color="success" onClick={AddCitie}>
                        <AddCircleOutlineRoundedIcon />
                      </IconButton>
                    ),
                  }}
                />
              </Stack>
            </ListItem>
          </List>
          <CitiesTable Cities={formik.values.Cities} DeleteCity={DeleteCity} />
        </DialogContent>
        <DialogActions sx={{ p: 2 }}>
          <Button type="submit" color="success" variant="contained">
            Submit
          </Button>
        </DialogActions>
      </form>
    </Dialog>
  );
}

const CitiesTable = ({ Cities, DeleteCity }) => {
  return (
    <Box sx={classes.BodyBox}>
      <Divider>
        <Typography sx={classes.Title}>Cities : </Typography>
      </Divider>
      <BodyCards Cities={Cities} DeleteCity={DeleteCity} />
    </Box>
  );
};

const BodyCards = ({ Cities, DeleteCity }) => {
  return (
    <Grid container spacing={2} sx={{ mt: "16px" }}>
      {Cities.map((city, index) => (
        <Grid item key={index}>
          <Chip
            color="secondary"
            label={city}
            variant="outlined"
            onDelete={() => DeleteCity(index)}
          />
        </Grid>
      ))}
    </Grid>
  );
};

const TypoHead = ({ item }) => {
  return <Typography sx={classes.Title}>{item.label}</Typography>;
};

const gridSize = {
  xs: 3,
  sm: 3,
  md: 3,
  lg: 3,
};

const items = [
  { label: "City Name", value: "cname" },
  { label: "Start Date", value: "startd" },
  { label: "End Date", value: "endd" },
  { label: "Actions", value: undefined },
];

const classes = {
  DialogTitle: {
    fontFamily: "Roboto",
    textTransform: "uppercase",
    fontWeight: "300",
    color: "secondary.main",
  },
  HeadBox: {
    m: 2,
    padding: "16px",
    backgroundColor: "#ededed52",
    borderRadius: "12px",
    outline: "1px solid",
    outlineColor: "#20AAE24f",
  },
  BodyBox: {
    m: 2,
    padding: "16px",
  },
  Title: {
    fontWeight: "400",
    fontSize: "13px",
  },
  Dialog: {
    "& .MuiDialog-paper": {
      borderRadius: { xs: "0", sm: "12px" },
    },
  },
};
