import { Box, Container, Grid, Paper, Slide } from "@mui/material";
import useMediaQuery from "@mui/material/useMediaQuery";
import React, { useEffect, useState } from "react";
import nomsgs from "../../../assets/pics/noMsgs.svg";
import { ClientsSide } from "./ClientsSide";
import MessagesSide from "./MessagesSide";

export default function Chat() {
  const [Livechat, setLivechat] = useState(null);

  const HandleLivechat = (id) => {
    const user = users?.filter((user) => user.id === id)[0] ?? {};
    setLivechat({ ...user });
  };

  useEffect(() => {
    if (users && users.length > 0) setLivechat(users[0]);
    else setLivechat(null);
  }, []);

  return (
    <Container maxWidth="xl" disableGutters>
      {users && users.length > 0 ? (
        <IfMsgs
          HandleLivechat={HandleLivechat}
          Livechat={Livechat}
          setLivechat={setLivechat}
        />
      ) : (
        <Nomsges />
      )}
    </Container>
  );
}

const IfMsgs = ({ HandleLivechat, Livechat, setLivechat }) => {
  const matches = useMediaQuery((theme) => theme.breakpoints.down("lg"));

  return (
    <Paper sx={classes.Paper}>
      <Grid container >
        {matches ? (
          <Grid item xs={12}>
            <MobileChat users={users} HandleLivechat={HandleLivechat} setLivechat={setLivechat} Livechat={Livechat} />
          </Grid>
        ) : (
          <>
            <Grid item xs={12} md={5} lg={4} sx={classes.ClientGrid}>
              <ClientsSide users={users} HandleLivechat={HandleLivechat} />
            </Grid>
            <Grid item xs={12} md={7} lg={8} sx={classes.MsgGrid}>
              <MessagesSide Livechat={Livechat} setLivechat={setLivechat} />
            </Grid>
          </>
        )}
      </Grid>
    </Paper>
  );
};

const MobileChat = ({ users, HandleLivechat, setLivechat,Livechat }) => {
  const [checked, setchecked] = useState(false);
  const OpenMsg = () => {
    setchecked(true);
  }
  const CloseMsg = () => {
    setchecked(false);
  }
  return (
    <>
      <Slide direction="left" in={checked} mountOnEnter unmountOnExit>
        <div>
        <MessagesSide Livechat={Livechat} setLivechat={setLivechat} CloseMsg={CloseMsg} />
        </div>
      </Slide>
      <Slide direction="right" in={!checked} mountOnEnter unmountOnExit>
        <div>
          <ClientsSide users={users} HandleLivechat={HandleLivechat} OpenMsg={OpenMsg}/>
        </div>
      </Slide>
    </>
  );
};

const Nomsges = () => {
  return <Box sx={classes.NomsgRoot}></Box>;
};

const classes = {
  NomsgRoot: {
    height: "calc(100vh - 96px)",
    width: "100%",
    background: `url(${nomsgs}) center no-repeat`,
    backgroundSize: "contain",
  },

  Paper: {
    maxHeight: "calc(100vh - 96px)",
    borderRadius: "1.5rem",
    overflow: "hidden",
    boxShadow: "rgba(149, 157, 165, 0.2) 0px 8px 24px",
  },
  ClientGrid: {
    height: "auto !important",
    minHeight: "calc(100vh - 96px) !important",
    borderRight: "1px solid rgba(0, 0, 0, 0.12)",
  },
  MsgGrid: {
    height: "auto !important",
    minHeight: "calc(100vh - 96px) !important",
    position: "relative",
  },
};

