import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import { Box, Divider, Grid, Paper, Typography, Stack } from "@mui/material";
import React from "react";
import LocalShippingRoundedIcon from "@mui/icons-material/LocalShippingRounded";
import { useTheme } from "@mui/material/styles";
import useMediaQuery from "@mui/material/useMediaQuery";
import Pagination from "@mui/material/Pagination";
import MoreVertRoundedIcon from "@mui/icons-material/MoreVertRounded";
const STATUS = { DELIVRED: "Delivred", OTW: "OTW" };

export default function TrackingItems() {
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down("md"));
  return (
    <>
      {matches ? <Mobile /> : <Web />}
      <Pagination
        count={10}
        variant="outlined"
        shape="rounded"
        sx={{
          "& .MuiPagination-ul": {
            justifyContent: "center",
          },
        }}
      />
    </>
  );
}

const Mobile = () => {
  return (
    <>
      <Grid
        container
        sx={{ py: "24px" }}
        spacing={2}
        justifyContent="space-evenly"
      >
        {items.map((item, index) => (
          <Grid item key={index} sx={{ flexGrow: 1 }}>
            <Box sx={classes.userCard}>
              <Stack direction="column" spacing={1}>
                <Stack
                  direction="row"
                  justifyContent="space-between"
                  alignItems="center"
                >
                  <Typography sx={classes.carduserName}>
                    {item.clientName}
                  </Typography>
                  <MoreVertRoundedIcon
                    sx={classes.MoreIcon}
                    style={{ padding: 0 }}
                  />
                </Stack>

                <Typography sx={classes.cardPhone}>{item.phone}</Typography>
                <Stack
                  direction="row"
                  spacing={2}
                  justifyContent="space-between"
                  alignItems="center"
                >
                  <Typography sx={classes.carduserName}>
                    {item.itemsNum}
                  </Typography>
                  <StatusHandler status={item.orderStatus} />
                </Stack>
              </Stack>
            </Box>
          </Grid>
        ))}
      </Grid>
    </>
  );
};

const Web = () => {
  return (
    <Paper sx={classes.paperRoot}>
      <HeadFactory />
      <Divider />
      <TableBody />
    </Paper>
  );
};

const HeadFactory = () => {
  return (
    <Grid container sx={{ py: "16px", paddingLeft: "16px" }} spacing={2}>
      {head.map((item, index) => (
        <Grid item xs={item.gridSize} key={index}>
          <Typography sx={classes.HeadTitle}>{item.label}</Typography>
        </Grid>
      ))}
    </Grid>
  );
};

const TableBody = () => {
  return items.map((item, index) => (
    <div key={index}>
      <Grid container sx={classes.gridHover} spacing={2} alignItems="center">
        <Grid item xs={1}>
          <Typography sx={classes.bodyitemsid}>{index + 1}</Typography>
        </Grid>
        <Grid item xs={2.75}>
          <Typography sx={classes.bodyitems} style={{ fontFamily: "Cairo" }}>
            {item.clientName}
          </Typography>
        </Grid>
        <Grid item xs={1.75}>
          <Typography sx={classes.bodyitems}>{item.itemsNum}</Typography>
        </Grid>
        <Grid item xs={2.75}>
          <Typography sx={classes.bodyitems}>{item.phone}</Typography>
        </Grid>
        <Grid item xs={3.75}>
          <Stack
            direction="row"
            justifyContent="space-between"
            alignItems="center"
          >
            <StatusHandler status={item.orderStatus} />
            <MoreVertRoundedIcon sx={classes.MoreIcon} />
          </Stack>
        </Grid>
      </Grid>
      {index + 1 === items.length ? false : <Divider />}
    </div>
  ));
};

const StatusHandler = ({ status }) => {
  switch (status) {
    case STATUS.DELIVRED:
      return (
        <Box sx={classes.DelivredBox}>
          <CheckCircleOutlineIcon />
          <Typography sx={classes.DelivredLabel}>Delivred</Typography>
        </Box>
      );
    case STATUS.OTW:
      return (
        <Box sx={classes.OTWBox}>
          <LocalShippingRoundedIcon />
          <Typography sx={classes.OTWLabel}>On The Way</Typography>
        </Box>
      );

    default:
      return <Typography>status not found</Typography>;
  }
};
