import { Paper, Stack, Typography, Divider } from "@mui/material";
import { useTheme } from "@mui/material/styles";
import useMediaQuery from "@mui/material/useMediaQuery";
import { Box } from "@mui/system";
import moment from "moment";
import React from "react";
import CosAvatar from "./CosAvatar";

export const ClientsSide = ({ users, HandleLivechat, OpenMsg }) => {
  return (
    <Box
      sx={{
        height: "100vh",
        maxHeight: "calc(100vh - 96px)",
        position: "relative",
      }}
    >
      <Box sx={classes.HeadBox}>
        <Typography sx={classes.Title}>Inbox</Typography>
      </Box>
      <Box sx={classes.ChatsBox}>
        <Stack direction="column" justifyContent="center" spacing={0}>
          {users?.map((user, index) => (
            <div key={index}>
              <ClientChatCard
                user={user}
                HandleLivechat={HandleLivechat}
                OpenMsg={OpenMsg}
              />
              <Divider />
            </div>
          ))}
        </Stack>
      </Box>
    </Box>
  );
};

const ClientChatCard = ({ user, HandleLivechat, OpenMsg }) => {
  const StartChatting = (id) => {
    HandleLivechat(id);
    if (OpenMsg) OpenMsg();
  };

  return (
    <Paper sx={classes.ClientChatCard} onClick={() => StartChatting(user.id)}>
      <Stack direction="row" alignItems="center" spacing={2} p="12px">
        <CosAvatar avatar={user.avatar} online={user.online} />
        <Stack direction="column" justifyContent="center" spacing={1}>
          <Typography sx={classes.ClientName}>{user.name}</Typography>
          <Typography sx={classes.ClientMessage}>
            {user.message[0].msg.substring(0, 10) + "..."}
          </Typography>
        </Stack>
        <Stack
          direction="column"
          justifyContent="center"
          alignItems="end"
          spacing={1}
          sx={{ flexGrow: 1 }}
        >
          <Typography sx={classes.Time}>
            {moment(user.sentat).fromNow()}
          </Typography>
          {/* <Typography sx={classes.notSeenMesseges}>5</Typography> */}
        </Stack>
      </Stack>
    </Paper>
  );
};

