import CampaignRoundedIcon from "@mui/icons-material/CampaignRounded";
import ForumRoundedIcon from "@mui/icons-material/ForumRounded";
import GridViewRoundedIcon from "@mui/icons-material/GridViewRounded";
import PeopleRoundedIcon from "@mui/icons-material/PeopleRounded";
import TourRoundedIcon from "@mui/icons-material/TourRounded";
import { styled } from "@mui/material";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import React from "react";
import { useMatch, useNavigate } from "react-router-dom";
import GroupWorkOutlinedIcon from '@mui/icons-material/GroupWorkOutlined';
const MyListItemText = styled(ListItemText)(({ theme, active }) => ({
  "& .MuiListItemText-primary": {
    textTransform: "uppercase",
    color: "rgb(99, 115, 129)",
    fontWeight: "300",
  },
  ...(active && {
    "& .MuiListItemText-primary": {
      color: theme.palette.primary.main,
      textTransform: "uppercase",
      fontWeight: "500",
    },
  }),
}));

const DrawerItems = () => {
  const navigate = useNavigate();
  const HandlMacth = (route) => {
    let match = useMatch(`${route}/*`);
    return match;
  };
  return (
    <List sx={{ py: 0 }}>
      {items.map((item, index) => (
        <ListItem
          button
          key={index}
          onClick={() => {
            navigate(item.link);
          }}
          sx={HandlMacth(item.link) ? classes.activeBorder : {}}
        >
          <ListItemIcon
            sx={
              HandlMacth(item.link)
                ? { color: "primary.main" }
                : { color: "rgb(99, 115, 129)" }
            }
          >
            {item.icon}
          </ListItemIcon>
          <MyListItemText primary={item.label} active={HandlMacth(item.link)} />
        </ListItem>
      ))}
    </List>
  );
};

export default DrawerItems;

const classes = {
  activeBorder: { backgroundColor:"primary.lightBg", borderLeft:"2px solid", borderColor:"primary.main" },
};

const items = [
  {
    label: "Overview",
    icon: <GridViewRoundedIcon />,
    link: "/",
    active: true,
  },
  {
    label: "Groups",
    link: "groups",
    icon: <GroupWorkOutlinedIcon />,
  },
  {
    label: "Tours",
    link: "tours",
    icon: <TourRoundedIcon />,
  },
  {
    label: "Users",
    link: "users",
    icon: <PeopleRoundedIcon />,
  },
  {
    label: "Chat",
    link: "chat",
    icon: <ForumRoundedIcon />,
  },
  {
    label: "Advertisement",
    link: "advertisement",
    icon: <CampaignRoundedIcon />,
  },
];
